import React, {Component} from "react";
import {Dropdown, FormControl} from 'react-bootstrap';

const CustomToggle = React.forwardRef(({ children, onClick }, ref) => (
  <a
    href=""
    ref={ref}
    onClick={e => {
      e.preventDefault();
      onClick(e);
    }}
  >
    {children}
    &#x25bc;
  </a>
));

// forwardRef again here!
// Dropdown needs access to the DOM of the Menu to measure it
const CustomMenu = React.forwardRef(
  ({ children, style, className, 'aria-labelledby': labeledBy }, ref) => {
    const [value, setValue] = React.useState('');

    return (
      <div
        ref={ref}
        style={style}
        className={className}
        aria-labelledby={labeledBy}
      >
        <FormControl
          autoFocus
          className="mx-3 my-2 w-auto"
          placeholder="Find Colour"
          onChange={e => setValue(e.target.value)}
          value={value}
        />
        <ul className="list-unstyled">
          {React.Children.toArray(children).filter(
            child =>
              !value || child.props.children.toLowerCase().startsWith(value),
          )}
        </ul>
      </div>
    );
  },
);
export default class ChooseColour extends Component{ 
  render(){
return(
  <Dropdown>
    <Dropdown.Toggle as={CustomToggle} id="dropdown-custom-components">
      Colours
    </Dropdown.Toggle>

    <Dropdown.Menu as={CustomMenu}>
      <Dropdown.Item eventKey="1">Black</Dropdown.Item>
      <Dropdown.Item eventKey="2">White</Dropdown.Item>
      <Dropdown.Item eventKey="3">Grey</Dropdown.Item>
      <Dropdown.Item eventKey="4">Blue</Dropdown.Item>
      <Dropdown.Item eventKey="5">Red</Dropdown.Item>
      <Dropdown.Item eventKey="6">Orange</Dropdown.Item>
      <Dropdown.Item eventKey="8">Yellow</Dropdown.Item>
      <Dropdown.Item eventKey="9" active>
        Orange
      </Dropdown.Item>
      <Dropdown.Item eventKey="10">Red-Orange</Dropdown.Item>
    </Dropdown.Menu>
  </Dropdown>
    )};
};  